"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { db } from "@/lib/firebase"
import { collection, addDoc, updateDoc, deleteDoc, doc, query, orderBy, onSnapshot, getDocs } from "firebase/firestore"
import { uploadToCloudinary, getOptimizedImageUrl } from "@/lib/cloudinary"
import { Star, MapPin, Users } from "lucide-react"
import {
  Plus,
  Edit,
  Trash2,
  Upload,
  X,
  Save,
  ArrowUp,
  ArrowDown,
  AlertCircle,
  CheckCircle,
  ImageIcon,
  User,
} from "lucide-react"

interface TeamMember {
  id: string
  name: string
  designation: string
  location: string
  image: string
  teamType: "national" | "state" | "district"
  state?: string
  district?: string
  order: number
  createdAt?: string
  updatedAt?: string
  imagePublicId?: string
}

export default function TeamDashboard() {
  const [teamMembers, setTeamMembers] = useState<TeamMember[]>([])
  const [editingMember, setEditingMember] = useState<TeamMember | null>(null)
  const [newMember, setNewMember] = useState({
    name: "",
    designation: "",
    location: "",
    image: "",
    teamType: "national" as "national" | "state" | "district",
    state: "",
    district: "",
    order: 0,
  })
  const [uploading, setUploading] = useState(false)
  const [loading, setLoading] = useState(true)
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [editingImageFile, setEditingImageFile] = useState<File | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)
  const [uploadProgress, setUploadProgress] = useState(0)
  const [previewUrl, setPreviewUrl] = useState<string | null>(null)
  const [editingPreviewUrl, setEditingPreviewUrl] = useState<string | null>(null)

  // Clear object URLs on unmount
  useEffect(() => {
    return () => {
      if (previewUrl) URL.revokeObjectURL(previewUrl)
      if (editingPreviewUrl) URL.revokeObjectURL(editingPreviewUrl)
    }
  }, [previewUrl, editingPreviewUrl])

  // Load team members on mount and setup real-time listener
  useEffect(() => {
    const loadTeamMembers = async () => {
      try {
        console.log("üîÑ Loading team members from Firestore...")
        setLoading(true)
        setError(null)

        // First, load initial data
        const q = query(collection(db, "teamMembers"), orderBy("order"))
        const querySnapshot = await getDocs(q)

        const members: TeamMember[] = []
        querySnapshot.forEach((doc) => {
          const data = doc.data()
          members.push({
            id: doc.id,
            name: data.name || "",
            designation: data.designation || "",
            location: data.location || "",
            image: data.image || "",
            teamType: data.teamType || "national",
            state: data.state || "",
            district: data.district || "",
            order: data.order || 0,
            createdAt: data.createdAt || "",
            updatedAt: data.updatedAt || "",
            imagePublicId: data.imagePublicId || "",
          } as TeamMember)
        })

        // Sort by order
        members.sort((a, b) => (a.order || 0) - (b.order || 0))
        setTeamMembers(members)
        console.log(`‚úÖ Loaded ${members.length} team members`)

        // Then setup real-time listener
        setupRealtimeListener()
      } catch (err: any) {
        console.error("‚ùå Error loading team members:", err)
        setError(`Failed to load team members: ${err.message}`)
        setLoading(false)
      }
    }

    const setupRealtimeListener = () => {
      try {
        console.log("üîÑ Setting up Firestore real-time listener...")

        const q = query(collection(db, "teamMembers"), orderBy("order"))

        const unsubscribe = onSnapshot(
          q,
          (querySnapshot) => {
            console.log("‚úÖ Firestore update received:", querySnapshot.size, "documents")

            const members: TeamMember[] = []
            querySnapshot.forEach((doc) => {
              const data = doc.data()
              members.push({
                id: doc.id,
                name: data.name || "",
                designation: data.designation || "",
                location: data.location || "",
                image: data.image || "",
                teamType: data.teamType || "national",
                state: data.state || "",
                district: data.district || "",
                order: data.order || 0,
                createdAt: data.createdAt || "",
                updatedAt: data.updatedAt || "",
                imagePublicId: data.imagePublicId || "",
              } as TeamMember)
            })

            // Sort by order
            members.sort((a, b) => (a.order || 0) - (b.order || 0))
            setTeamMembers(members)
            setLoading(false)

            // Log Cloudinary URLs
            const cloudinaryMembers = members.filter((m) => m.image && m.image.includes("cloudinary.com"))
            console.log(`üìä ${cloudinaryMembers.length}/${members.length} members have Cloudinary images`)
          },
          (error) => {
            console.error("‚ùå Error in Firestore listener:", error)
            setError(`Real-time updates paused: ${error.message}`)
          },
        )

        return unsubscribe
      } catch (err: any) {
        console.error("‚ùå Setup error:", err)
        setError(`Setup error: ${err.message}`)
        setLoading(false)
        return () => {}
      }
    }

    let unsubscribe: () => void = () => {}

    const init = async () => {
      await loadTeamMembers()
      unsubscribe = setupRealtimeListener()
    }

    init()

    return () => {
      console.log("üßπ Cleaning up Firestore listener")
      unsubscribe()
    }
  }, [])

  // FIXED: Enhanced image upload function with proper Cloudinary response handling
  const uploadImageToCloudinary = async (file: File): Promise<{url: string, publicId: string}> => {
    setUploading(true)
    setUploadProgress(0)
    setError(null)

    try {
      console.log("üì§ Starting Cloudinary upload...")

      // Validate file
      if (!file.type.startsWith("image/")) {
        throw new Error("Please select an image file (JPEG, PNG, WebP, etc.)")
      }

      if (file.size > 5 * 1024 * 1024) {
        throw new Error("Image size should be less than 5MB")
      }

      // Simulate upload progress
      const progressInterval = setInterval(() => {
        setUploadProgress((prev) => {
          if (prev >= 90) {
            clearInterval(progressInterval)
            return 90
          }
          return prev + 10
        })
      }, 200)

      // FIX: uploadToCloudinary should return an object with url and publicId
      const cloudinaryResult = await uploadToCloudinary(file)

      clearInterval(progressInterval)
      setUploadProgress(100)

      console.log("‚úÖ Cloudinary upload successful:", cloudinaryResult)

      // Handle different response formats
      let imageUrl = ""
      let publicId = ""

      if (typeof cloudinaryResult === "string") {
        // If it's just a string URL
        imageUrl = cloudinaryResult
        // Try to extract public_id from URL
        if (imageUrl.includes("cloudinary.com")) {
          try {
            const urlObj = new URL(imageUrl)
            const pathParts = urlObj.pathname.split('/')
            const uploadIndex = pathParts.indexOf('upload')
            if (uploadIndex !== -1) {
              const afterUpload = pathParts.slice(uploadIndex + 1).join('/')
              const withoutExtension = afterUpload.replace(/\.[^/.]+$/, '')
              // Remove version prefix (v1234567/)
              publicId = withoutExtension.replace(/^v\d+\//, '')
            }
          } catch (e) {
            console.warn("Could not parse publicId from URL:", e)
          }
        }
      } else if (cloudinaryResult && typeof cloudinaryResult === "object") {
        // If it's an object with secure_url or url property
        imageUrl = cloudinaryResult.secure_url || cloudinaryResult.url || ""
        publicId = cloudinaryResult.public_id || ""
        
        // If we have the Cloudinary response but no public_id, try to extract it
        if (!publicId && imageUrl.includes("cloudinary.com")) {
          try {
            const urlObj = new URL(imageUrl)
            const pathParts = urlObj.pathname.split('/')
            const uploadIndex = pathParts.indexOf('upload')
            if (uploadIndex !== -1) {
              const afterUpload = pathParts.slice(uploadIndex + 1).join('/')
              const withoutExtension = afterUpload.replace(/\.[^/.]+$/, '')
              publicId = withoutExtension.replace(/^v\d+\//, '')
            }
          } catch (e) {
            console.warn("Could not parse publicId from URL:", e)
          }
        }
      } else {
        throw new Error("Invalid response from Cloudinary upload")
      }

      // Validate that we got a proper URL
      if (!imageUrl || !imageUrl.startsWith("http")) {
        throw new Error("Failed to get valid image URL from Cloudinary")
      }

      console.log("‚úÖ Cloudinary upload complete - URL:", imageUrl, "Public ID:", publicId)

      // Wait a bit to show 100% progress
      await new Promise((resolve) => setTimeout(resolve, 500))

      return { url: imageUrl, publicId }
    } catch (error: any) {
      console.error("‚ùå Error uploading image:", error)
      throw new Error(`Failed to upload image: ${error.message || "Unknown error"}`)
    } finally {
      setUploading(false)
      setTimeout(() => setUploadProgress(0), 1000)
    }
  }

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>, isEditing = false) => {
    const file = e.target.files?.[0]
    if (!file) return

    // Reset previous errors
    setError(null)

    // Validate file
    if (!file.type.startsWith("image/")) {
      setError("Please select an image file (JPEG, PNG, WebP, etc.)")
      return
    }

    if (file.size > 5 * 1024 * 1024) {
      setError("Image size should be less than 5MB")
      return
    }

    // Create preview
    const objectUrl = URL.createObjectURL(file)

    if (isEditing) {
      setEditingImageFile(file)
      setEditingPreviewUrl(objectUrl)
    } else {
      setImageFile(file)
      setPreviewUrl(objectUrl)
      setNewMember((prev) => ({ ...prev, image: objectUrl }))
    }
  }

  // FIXED: handleAddMember with proper Cloudinary response handling
  const handleAddMember = async () => {
    console.log("‚ûï Adding new team member...")

    // Reset messages
    setError(null)
    setSuccess(null)

    // Validate required fields
    if (!newMember.name.trim()) {
      setError("Please enter team member name")
      return
    }

    if (!newMember.designation.trim()) {
      setError("Please enter designation")
      return
    }

    if (!newMember.location.trim()) {
      setError("Please enter location")
      return
    }

    // Additional validation based on team type
    if (newMember.teamType === "state" && !newMember.state?.trim()) {
      setError("Please enter state name")
      return
    }

    if (newMember.teamType === "district" && !newMember.district?.trim()) {
      setError("Please enter district name")
      return
    }

    try {
      setUploading(true)

      let imageUrl = ""
      let imagePublicId = ""

      // Upload image if file is selected
      if (imageFile) {
        try {
          console.log("üì§ Uploading image to Cloudinary...")
          const cloudinaryResult = await uploadImageToCloudinary(imageFile)
          imageUrl = cloudinaryResult.url
          imagePublicId = cloudinaryResult.publicId
          console.log("‚úÖ Image uploaded to Cloudinary:", imageUrl)
          console.log("üìù Public ID:", imagePublicId)
        } catch (uploadError: any) {
          console.error("‚ùå Image upload failed:", uploadError)
          setError(`Image upload failed: ${uploadError.message}`)
          setUploading(false)
          return
        }
      } else if (newMember.image && newMember.image.startsWith("blob:")) {
        // If only blob URL exists without file, show error
        setError("Please select an image file to upload")
        setUploading(false)
        return
      } else {
        imageUrl = newMember.image || ""
      }

      // Calculate order
      const highestOrder = teamMembers.length > 0 ? Math.max(...teamMembers.map((m) => m.order || 0)) : 0

      // Prepare member data for Firebase
      const memberData: any = {
        name: newMember.name.trim(),
        designation: newMember.designation.trim(),
        location: newMember.location.trim(),
        image: imageUrl,
        teamType: newMember.teamType,
        order: highestOrder + 1,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      }

      // Add imagePublicId if available
      if (imagePublicId) {
        memberData.imagePublicId = imagePublicId
      }

      // Add state/district only if applicable
      if (newMember.teamType === "state") {
        memberData.state = newMember.state?.trim() || ""
      }

      if (newMember.teamType === "district") {
        memberData.district = newMember.district?.trim() || ""
      }

      console.log("üìù Saving to Firestore:", memberData)

      // Validate Firebase connection
      if (!db) {
        throw new Error("Firebase is not initialized")
      }

      const docRef = await addDoc(collection(db, "teamMembers"), memberData)
      console.log("‚úÖ Document written with ID:", docRef.id)

      // Reset form
      clearForm()

      setSuccess("‚úÖ Team member added successfully! Changes will appear on the team page in real-time.")
      setTimeout(() => setSuccess(null), 5000)
    } catch (error: any) {
      console.error("‚ùå Error adding team member:", error)
      setError(`Failed to add team member: ${error.message}`)
    } finally {
      setUploading(false)
    }
  }

  // FIXED: handleUpdateMember with proper Cloudinary response handling
  const handleUpdateMember = async () => {
    if (!editingMember) return

    console.log("‚úèÔ∏è Updating team member:", editingMember.id)

    setError(null)
    setSuccess(null)
    setUploading(true)

    try {
      let imageUrl = editingMember.image || ""
      let imagePublicId = editingMember.imagePublicId || ""

      // Upload new image if file is selected
      if (editingImageFile) {
        try {
          console.log("üì§ Uploading new image to Cloudinary...")
          const cloudinaryResult = await uploadImageToCloudinary(editingImageFile)
          imageUrl = cloudinaryResult.url
          imagePublicId = cloudinaryResult.publicId
          console.log("‚úÖ New image uploaded:", imageUrl)
        } catch (uploadError: any) {
          console.error("‚ùå Image upload failed:", uploadError)
          setError(`Image upload failed: ${uploadError.message}`)
          setUploading(false)
          return
        }
      }

      const memberData: any = {
        name: editingMember.name.trim(),
        designation: editingMember.designation.trim(),
        location: editingMember.location.trim(),
        image: imageUrl,
        teamType: editingMember.teamType,
        order: editingMember.order || 0,
        updatedAt: new Date().toISOString(),
      }

      // Add imagePublicId if available
      if (imagePublicId) {
        memberData.imagePublicId = imagePublicId
      }

      // Add state/district only if applicable
      if (editingMember.teamType === "state") {
        memberData.state = editingMember.state?.trim() || ""
      }

      if (editingMember.teamType === "district") {
        memberData.district = editingMember.district?.trim() || ""
      }

      console.log("üìù Updating Firestore document:", editingMember.id, memberData)

      const memberRef = doc(db, "teamMembers", editingMember.id)
      await updateDoc(memberRef, memberData)

      setEditingMember(null)
      setEditingImageFile(null)
      if (editingPreviewUrl) {
        URL.revokeObjectURL(editingPreviewUrl)
        setEditingPreviewUrl(null)
      }

      setSuccess("‚úÖ Team member updated successfully! Changes will appear on the team page in real-time.")
      setTimeout(() => setSuccess(null), 5000)
    } catch (error: any) {
      console.error("‚ùå Error updating team member:", error)
      setError(`Failed to update team member: ${error.message}`)
    } finally {
      setUploading(false)
    }
  }

  const handleDeleteMember = async (id: string, imagePublicId?: string) => {
    if (!confirm("Are you sure you want to delete this team member? This action cannot be undone.")) return

    try {
      console.log("üóëÔ∏è Deleting team member:", id)
      if (imagePublicId) {
        console.log("üìù Associated Cloudinary public_id:", imagePublicId)
        // Note: To delete from Cloudinary, you would need a server-side API
      }

      await deleteDoc(doc(db, "teamMembers", id))
      setSuccess("‚úÖ Team member deleted successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (error: any) {
      console.error("Error deleting team member:", error)
      setError(`Failed to delete team member: ${error.message}`)
    }
  }

  const handleReorder = async (index: number, direction: "up" | "down") => {
    const members = [...teamMembers]
    const targetIndex = direction === "up" ? index - 1 : index + 1

    if (targetIndex < 0 || targetIndex >= members.length) return

    try {
      // Swap orders
      const member1 = members[index]
      const member2 = members[targetIndex]

      // Swap orders locally first for instant feedback
      const tempOrder = member1.order
      member1.order = member2.order
      member2.order = tempOrder

      // Sort and update state
      const sortedMembers = [...members].sort((a, b) => (a.order || 0) - (b.order || 0))
      setTeamMembers(sortedMembers)

      // Update in Firebase
      await Promise.all([
        updateDoc(doc(db, "teamMembers", member1.id), {
          order: member1.order,
          updatedAt: new Date().toISOString(),
        }),
        updateDoc(doc(db, "teamMembers", member2.id), {
          order: member2.order,
          updatedAt: new Date().toISOString(),
        }),
      ])

      console.log("üîÑ Order updated successfully")
    } catch (error: any) {
      console.error("Error updating order:", error)
      setError("Failed to update order. Please refresh the page.")
    }
  }

  const clearForm = () => {
    setNewMember({
      name: "",
      designation: "",
      location: "",
      image: "",
      teamType: "national",
      state: "",
      district: "",
      order: 0,
    })
    setImageFile(null)
    if (previewUrl) {
      URL.revokeObjectURL(previewUrl)
      setPreviewUrl(null)
    }
    setError(null)
  }

  const clearEditingForm = () => {
    setEditingMember(null)
    setEditingImageFile(null)
    if (editingPreviewUrl) {
      URL.revokeObjectURL(editingPreviewUrl)
      setEditingPreviewUrl(null)
    }
  }

  const filteredMembers = (type: TeamMember["teamType"]) => {
    return teamMembers.filter((member) => member.teamType === type)
  }

  // Add debug function to test Cloudinary
  const testCloudinaryConnection = async () => {
    console.log("üß™ Testing Cloudinary connection...")
    console.log("Cloud Name:", process.env.NEXT_PUBLIC_CLOUDINARY_CLOUD_NAME)
    console.log("Upload Preset:", process.env.NEXT_PUBLIC_CLOUDINARY_UPLOAD_PRESET ? "Set" : "Not set")
  }

  // Run test on component mount
  useEffect(() => {
    testCloudinaryConnection()
  }, [])

  if (loading && teamMembers.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 p-8">
        <div className="max-w-7xl mx-auto">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black mx-auto"></div>
            <p className="mt-4 text-gray-600">Loading dashboard...</p>
            <p className="text-sm text-gray-500 mt-2">Connecting to Firestore...</p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4 sm:p-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Team Management Dashboard</h1>
          <p className="text-gray-600">Manage your team members across national, state, and district levels</p>
          <div className="flex flex-wrap items-center gap-2 mt-2">
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
              <p className="text-sm text-green-600 font-medium">Real-time updates enabled</p>
            </div>
            <span className="text-gray-400">‚Ä¢</span>
            <p className="text-sm text-gray-500">
              Cloudinary: {process.env.NEXT_PUBLIC_CLOUDINARY_CLOUD_NAME ? "‚úÖ Configured" : "‚ùå Not configured"}
            </p>
            <span className="text-gray-400">‚Ä¢</span>
            <p className="text-sm text-gray-500">
              {teamMembers.length} team members | {teamMembers.filter((m) => m.image).length} with images
            </p>
          </div>
        </div>

        {/* Messages */}
        {error && (
          <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center gap-3">
            <AlertCircle className="text-red-500 flex-shrink-0" size={20} />
            <p className="text-red-700 flex-1">{error}</p>
            <button onClick={() => setError(null)} className="ml-2 p-1 text-red-500 hover:text-red-700">
              <X size={18} />
            </button>
          </div>
        )}

        {success && (
          <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg flex items-center gap-3">
            <CheckCircle className="text-green-500 flex-shrink-0" size={20} />
            <p className="text-green-700 flex-1">{success}</p>
            <button onClick={() => setSuccess(null)} className="ml-2 p-1 text-green-500 hover:text-green-700">
              <X size={18} />
            </button>
          </div>
        )}

        {/* Upload Progress */}
        {uploading && uploadProgress > 0 && (
          <div className="mb-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
            <div className="flex items-center justify-between mb-2">
              <span className="text-sm font-medium text-blue-700 flex items-center gap-2">
                <ImageIcon size={16} />
                {uploadProgress < 100 ? "Uploading to Cloudinary..." : "Processing image..."}
              </span>
              <span className="text-sm font-bold text-blue-700">{uploadProgress}%</span>
            </div>
            <div className="w-full bg-blue-200 rounded-full h-2">
              <div
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${uploadProgress}%` }}
              ></div>
            </div>
            {uploadProgress === 100 && (
              <p className="text-xs text-green-600 mt-2">‚úì Processing Cloudinary transformations...</p>
            )}
          </div>
        )}

        {/* Add New Member Form */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-8">
          <h2 className="text-xl font-bold text-gray-900 mb-4">Add New Team Member</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {/* Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Name *</label>
              <input
                type="text"
                value={newMember.name}
                onChange={(e) => setNewMember({ ...newMember, name: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                placeholder="Enter full name"
                disabled={uploading}
              />
            </div>

            {/* Designation */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Designation *</label>
              <input
                type="text"
                value={newMember.designation}
                onChange={(e) => setNewMember({ ...newMember, designation: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                placeholder="Enter designation"
                disabled={uploading}
              />
            </div>

            {/* Location */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Location *</label>
              <input
                type="text"
                value={newMember.location}
                onChange={(e) => setNewMember({ ...newMember, location: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                placeholder="Enter location"
                disabled={uploading}
              />
            </div>

            {/* Team Type */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Team Type *</label>
              <select
                value={newMember.teamType}
                onChange={(e) => {
                  const teamType = e.target.value as "national" | "state" | "district"
                  setNewMember({
                    ...newMember,
                    teamType,
                    state: teamType === "state" ? newMember.state : "",
                    district: teamType === "district" ? newMember.district : "",
                  })
                }}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                disabled={uploading}
              >
                <option value="national">National Leadership</option>
                <option value="state">State Team</option>
                <option value="district">District Team</option>
              </select>
            </div>

            {/* State Field (conditional) */}
            {newMember.teamType === "state" && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">State *</label>
                <input
                  type="text"
                  value={newMember.state}
                  onChange={(e) => setNewMember({ ...newMember, state: e.target.value })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                  placeholder="Enter state name"
                  disabled={uploading}
                />
              </div>
            )}

            {/* District Field (conditional) */}
            {newMember.teamType === "district" && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">District *</label>
                <input
                  type="text"
                  value={newMember.district}
                  onChange={(e) => setNewMember({ ...newMember, district: e.target.value })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                  placeholder="Enter district name"
                  disabled={uploading}
                />
              </div>
            )}

            {/* Image Upload */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-1">Profile Image</label>
              <div className="flex flex-col gap-4">
                <div className="flex items-center gap-4">
                  <label
                    className={`flex items-center gap-2 px-4 py-2 rounded-lg cursor-pointer transition-colors ${uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"}`}
                  >
                    <Upload size={18} />
                    {uploading ? "Uploading..." : "Select Image"}
                    <input
                      type="file"
                      accept="image/*"
                      onChange={(e) => handleFileChange(e, false)}
                      className="hidden"
                      disabled={uploading}
                    />
                  </label>
                  {imageFile && (
                    <div className="flex items-center gap-2">
                      <span className="text-sm text-gray-600">
                        Selected: {imageFile.name} ({(imageFile.size / 1024).toFixed(2)} KB)
                      </span>
                      <button
                        onClick={() => {
                          setImageFile(null)
                          setNewMember({ ...newMember, image: "" })
                          if (previewUrl) {
                            URL.revokeObjectURL(previewUrl)
                            setPreviewUrl(null)
                          }
                        }}
                        className="p-1 text-red-500 hover:text-red-700"
                        disabled={uploading}
                      >
                        <X size={16} />
                      </button>
                    </div>
                  )}
                </div>

                {/* Image Preview */}
                {(previewUrl || newMember.image) && (
                  <div className="mt-2">
                    <p className="text-sm font-medium text-gray-700 mb-2">Preview:</p>
                    <div className="flex flex-wrap items-start gap-4">
                      <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                        {previewUrl ? (
                          <img
                            src={previewUrl || "/placeholder.svg"}
                            alt="Preview"
                            className="w-full h-full object-cover"
                          />
                        ) : newMember.image ? (
                          <img
                            src={newMember.image || "/placeholder.svg"}
                            alt="Preview"
                            className="w-full h-full object-cover"
                            onError={(e) => {
                              ;(e.target as HTMLImageElement).style.display = "none"
                              const parent = e.target.parentElement
                              if (parent) {
                                parent.innerHTML =
                                  '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">No Image</div>'
                              }
                            }}
                          />
                        ) : null}
                      </div>
                      <div className="text-sm text-gray-600">
                        <p className="font-medium">Image Details:</p>
                        <p>‚Ä¢ Will be uploaded to Cloudinary</p>
                        <p>‚Ä¢ Automatically optimized (WebP, 500√ó500)</p>
                        <p>‚Ä¢ Stored in "team-members" folder</p>
                      </div>
                    </div>
                  </div>
                )}
              </div>
              <p className="text-xs text-gray-500 mt-2">
                {uploading
                  ? "Uploading image to Cloudinary..."
                  : "Recommended: JPG, PNG, or WebP. Max 5MB. Images are optimized and stored on Cloudinary."}
              </p>
            </div>
          </div>

          {/* Form Actions */}
          <div className="mt-6 flex items-center gap-4">
            <button
              onClick={handleAddMember}
              disabled={uploading}
              className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"
              }`}
            >
              {uploading ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                  Processing...
                </>
              ) : (
                <>
                  <Plus size={18} />
                  Add Team Member
                </>
              )}
            </button>
            <button
              onClick={clearForm}
              disabled={uploading}
              className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors disabled:opacity-50"
            >
              Clear Form
            </button>
          </div>
        </div>

        {/* Team Members Display */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* National Team */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Star className="text-yellow-500" size={20} />
                  <h2 className="text-xl font-bold text-gray-900">National Leadership</h2>
                </div>
                <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                  {filteredMembers("national").length} members
                </span>
              </div>
            </div>
            <div className="p-6 space-y-4 max-h-[600px] overflow-y-auto">
              {filteredMembers("national").map((member, index) => (
                <div
                  key={member.id}
                  className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors"
                >
                  <div className="flex items-center justify-between mb-3">
                    <div className="flex items-center gap-3">
                      <div className="relative w-12 h-12 rounded-lg overflow-hidden bg-gray-100 flex-shrink-0">
                        {member.image ? (
                          <img
                            src={
                              member.image.includes("cloudinary.com")
                                ? getOptimizedImageUrl(member.image, { width: 100, height: 100 })
                                : member.image
                            }
                            alt={member.name}
                            className="w-full h-full object-cover"
                            onError={(e) => {
                              ;(e.target as HTMLImageElement).style.display = "none"
                              const parent = e.target.parentElement
                              if (parent) {
                                parent.innerHTML =
                                  '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500"><User size={20} /></div>'
                              }
                            }}
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">
                            <User size={20} />
                          </div>
                        )}
                      </div>
                      <div className="min-w-0 flex-1">
                        <h3 className="font-semibold text-gray-900 truncate">{member.name}</h3>
                        <p className="text-sm text-gray-600 truncate">{member.designation}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2 flex-shrink-0">
                      <button
                        onClick={() => setEditingMember(member)}
                        className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                        title="Edit"
                      >
                        <Edit size={16} />
                      </button>
                      <button
                        onClick={() => handleDeleteMember(member.id, member.imagePublicId)}
                        className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                        title="Delete"
                      >
                        <Trash2 size={16} />
                      </button>
                    </div>
                  </div>
                  <div className="flex items-center justify-between text-sm text-gray-600">
                    <div className="flex items-center gap-2">
                      <span>Order: {member.order}</span>
                      <MapPin size={12} />
                      <span className="truncate max-w-[100px]">{member.location}</span>
                    </div>
                    <div className="flex items-center gap-1">
                      <button
                        onClick={() => {
                          const globalIndex = teamMembers.findIndex((m) => m.id === member.id)
                          handleReorder(globalIndex, "up")
                        }}
                        className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                        disabled={index === 0}
                        title="Move up"
                      >
                        <ArrowUp size={14} />
                      </button>
                      <button
                        onClick={() => {
                          const globalIndex = teamMembers.findIndex((m) => m.id === member.id)
                          handleReorder(globalIndex, "down")
                        }}
                        className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                        disabled={index === filteredMembers("national").length - 1}
                        title="Move down"
                      >
                        <ArrowDown size={14} />
                      </button>
                    </div>
                  </div>
                  {member.image && member.image.includes("cloudinary.com") && (
                    <div className="mt-2">
                      <span className="inline-flex items-center gap-1 px-2 py-1 text-xs bg-green-100 text-green-800 rounded-full">
                        <ImageIcon size={10} />
                        Cloudinary
                      </span>
                      {member.imagePublicId && (
                        <p className="text-xs text-gray-500 mt-1 truncate">ID: {member.imagePublicId.substring(0, 30)}...</p>
                      )}
                    </div>
                  )}
                </div>
              ))}
              {filteredMembers("national").length === 0 && (
                <div className="text-center py-8 text-gray-500">
                  <User className="mx-auto mb-2 text-gray-400" />
                  No national team members yet
                </div>
              )}
            </div>
          </div>

          {/* State Teams */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <MapPin className="text-blue-500" size={20} />
                  <h2 className="text-xl font-bold text-gray-900">State Teams</h2>
                </div>
                <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                  {filteredMembers("state").length} members
                </span>
              </div>
            </div>
            <div className="p-6 space-y-4 max-h-[600px] overflow-y-auto">
              {filteredMembers("state").map((member) => (
                <div
                  key={member.id}
                  className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors"
                >
                  <div className="flex items-center justify-between mb-3">
                    <div className="flex items-center gap-3">
                      <div className="relative w-12 h-12 rounded-lg overflow-hidden bg-gray-100 flex-shrink-0">
                        {member.image ? (
                          <img
                            src={
                              member.image.includes("cloudinary.com")
                                ? getOptimizedImageUrl(member.image, { width: 100, height: 100 })
                                : member.image
                            }
                            alt={member.name}
                            className="w-full h-full object-cover"
                            onError={(e) => {
                              ;(e.target as HTMLImageElement).style.display = "none"
                              const parent = e.target.parentElement
                              if (parent) {
                                parent.innerHTML =
                                  '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500"><User size={20} /></div>'
                              }
                            }}
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">
                            <User size={20} />
                          </div>
                        )}
                      </div>
                      <div className="min-w-0 flex-1">
                        <h3 className="font-semibold text-gray-900 truncate">{member.name}</h3>
                        <p className="text-sm text-gray-600 truncate">{member.designation}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2 flex-shrink-0">
                      <button
                        onClick={() => setEditingMember(member)}
                        className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                      >
                        <Edit size={16} />
                      </button>
                      <button
                        onClick={() => handleDeleteMember(member.id, member.imagePublicId)}
                        className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                      >
                        <Trash2 size={16} />
                      </button>
                    </div>
                  </div>
                  <div className="text-sm text-gray-600 space-y-1">
                    <p className="truncate">
                      <span className="font-medium">State:</span> {member.state || "Not specified"}
                    </p>
                    <p className="truncate">
                      <span className="font-medium">Location:</span> {member.location}
                    </p>
                    <p className="truncate">
                      <span className="font-medium">Order:</span> {member.order}
                    </p>
                  </div>
                  {member.image && member.image.includes("cloudinary.com") && (
                    <div className="mt-2">
                      <span className="inline-flex items-center gap-1 px-2 py-1 text-xs bg-green-100 text-green-800 rounded-full">
                        <ImageIcon size={10} />
                        Cloudinary
                      </span>
                    </div>
                  )}
                </div>
              ))}
              {filteredMembers("state").length === 0 && (
                <div className="text-center py-8 text-gray-500">
                  <MapPin className="mx-auto mb-2 text-gray-400" />
                  No state team members yet
                </div>
              )}
            </div>
          </div>

          {/* District Teams */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Users className="text-purple-500" size={20} />
                  <h2 className="text-xl font-bold text-gray-900">District Teams</h2>
                </div>
                <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                  {filteredMembers("district").length} members
                </span>
              </div>
            </div>
            <div className="p-6 space-y-4 max-h-[600px] overflow-y-auto">
              {filteredMembers("district").map((member) => (
                <div
                  key={member.id}
                  className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors"
                >
                  <div className="flex items-center justify-between mb-3">
                    <div className="flex items-center gap-3">
                      <div className="relative w-12 h-12 rounded-lg overflow-hidden bg-gray-100 flex-shrink-0">
                        {member.image ? (
                          <img
                            src={
                              member.image.includes("cloudinary.com")
                                ? getOptimizedImageUrl(member.image, { width: 100, height: 100 })
                                : member.image
                            }
                            alt={member.name}
                            className="w-full h-full object-cover"
                            onError={(e) => {
                              ;(e.target as HTMLImageElement).style.display = "none"
                              const parent = e.target.parentElement
                              if (parent) {
                                parent.innerHTML =
                                  '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500"><User size={20} /></div>'
                              }
                            }}
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">
                            <User size={20} />
                          </div>
                        )}
                      </div>
                      <div className="min-w-0 flex-1">
                        <h3 className="font-semibold text-gray-900 truncate">{member.name}</h3>
                        <p className="text-sm text-gray-600 truncate">{member.designation}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2 flex-shrink-0">
                      <button
                        onClick={() => setEditingMember(member)}
                        className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                      >
                        <Edit size={16} />
                      </button>
                      <button
                        onClick={() => handleDeleteMember(member.id, member.imagePublicId)}
                        className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                      >
                        <Trash2 size={16} />
                      </button>
                    </div>
                  </div>
                  <div className="text-sm text-gray-600 space-y-1">
                    <p className="truncate">
                      <span className="font-medium">District:</span> {member.district || "Not specified"}
                    </p>
                    <p className="truncate">
                      <span className="font-medium">Location:</span> {member.location}
                    </p>
                    <p className="truncate">
                      <span className="font-medium">Order:</span> {member.order}
                    </p>
                  </div>
                  {member.image && member.image.includes("cloudinary.com") && (
                    <div className="mt-2">
                      <span className="inline-flex items-center gap-1 px-2 py-1 text-xs bg-green-100 text-green-800 rounded-full">
                        <ImageIcon size={10} />
                        Cloudinary
                      </span>
                    </div>
                  )}
                </div>
              ))}
              {filteredMembers("district").length === 0 && (
                <div className="text-center py-8 text-gray-500">
                  <Users className="mx-auto mb-2 text-gray-400" />
                  No district team members yet
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Edit Modal */}
        {editingMember && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-xl shadow-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
              <div className="p-6 border-b border-gray-200">
                <div className="flex items-center justify-between">
                  <h2 className="text-2xl font-bold text-gray-900">Edit Team Member</h2>
                  <button onClick={clearEditingForm} className="p-2 hover:bg-gray-100 rounded-lg" disabled={uploading}>
                    <X size={24} />
                  </button>
                </div>
              </div>
              <div className="p-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Name *</label>
                    <input
                      type="text"
                      value={editingMember.name}
                      onChange={(e) => setEditingMember({ ...editingMember, name: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      disabled={uploading}
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Designation *</label>
                    <input
                      type="text"
                      value={editingMember.designation}
                      onChange={(e) => setEditingMember({ ...editingMember, designation: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      disabled={uploading}
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Location *</label>
                    <input
                      type="text"
                      value={editingMember.location}
                      onChange={(e) => setEditingMember({ ...editingMember, location: e.target.value })}
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      disabled={uploading}
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Team Type *</label>
                    <select
                      value={editingMember.teamType}
                      onChange={(e) =>
                        setEditingMember({
                          ...editingMember,
                          teamType: e.target.value as TeamMember["teamType"],
                          state: e.target.value === "state" ? editingMember.state : "",
                          district: e.target.value === "district" ? editingMember.district : "",
                        })
                      }
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      disabled={uploading}
                    >
                      <option value="national">National Leadership</option>
                      <option value="state">State Team</option>
                      <option value="district">District Team</option>
                    </select>
                  </div>
                  {editingMember.teamType === "state" && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">State *</label>
                      <input
                        type="text"
                        value={editingMember.state || ""}
                        onChange={(e) => setEditingMember({ ...editingMember, state: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        disabled={uploading}
                      />
                    </div>
                  )}
                  {editingMember.teamType === "district" && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">District *</label>
                      <input
                        type="text"
                        value={editingMember.district || ""}
                        onChange={(e) => setEditingMember({ ...editingMember, district: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        disabled={uploading}
                      />
                    </div>
                  )}
                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">Profile Image</label>
                    <div className="space-y-4">
                      <div className="flex items-center gap-4">
                        <label
                          className={`flex items-center gap-2 px-4 py-2 rounded-lg cursor-pointer transition-colors ${uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"}`}
                        >
                          <Upload size={18} />
                          {uploading ? "Uploading..." : "Upload New Image"}
                          <input
                            type="file"
                            accept="image/*"
                            onChange={(e) => handleFileChange(e, true)}
                            className="hidden"
                            disabled={uploading}
                          />
                        </label>
                        {editingImageFile && (
                          <div className="flex items-center gap-2">
                            <span className="text-sm text-gray-600">
                              New: {editingImageFile.name} ({(editingImageFile.size / 1024).toFixed(2)} KB)
                            </span>
                            <button
                              onClick={() => {
                                setEditingImageFile(null)
                                if (editingPreviewUrl) {
                                  URL.revokeObjectURL(editingPreviewUrl)
                                  setEditingPreviewUrl(null)
                                }
                              }}
                              className="p-1 text-red-500 hover:text-red-700"
                              disabled={uploading}
                            >
                              <X size={16} />
                            </button>
                          </div>
                        )}
                      </div>

                      {/* Current Image Preview */}
                      {editingMember.image && !editingPreviewUrl && (
                        <div>
                          <p className="text-sm font-medium text-gray-700 mb-2">Current Image:</p>
                          <div className="flex flex-wrap items-start gap-4">
                            <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                              <img
                                src={
                                  editingMember.image.includes("cloudinary.com")
                                    ? getOptimizedImageUrl(editingMember.image, { width: 200, height: 200 })
                                    : editingMember.image
                                }
                                alt="Current"
                                className="w-full h-full object-cover"
                                onError={(e) => {
                                  ;(e.target as HTMLImageElement).style.display = "none"
                                  const parent = e.target.parentElement
                                  if (parent) {
                                    parent.innerHTML =
                                      '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">No Image</div>'
                                  }
                                }}
                              />
                            </div>
                            <div className="text-sm text-gray-600">
                              <p className="font-medium">Current Image Info:</p>
                              <p>
                                ‚Ä¢ Source: {editingMember.image.includes("cloudinary.com") ? "Cloudinary" : "External"}
                              </p>
                              {editingMember.imagePublicId && (
                                <p className="truncate max-w-xs text-xs">Public ID: {editingMember.imagePublicId}</p>
                              )}
                            </div>
                          </div>
                        </div>
                      )}

                      {/* New Image Preview */}
                      {editingPreviewUrl && (
                        <div>
                          <p className="text-sm font-medium text-gray-700 mb-2">New Image Preview:</p>
                          <div className="flex flex-wrap items-start gap-4">
                            <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                              <img
                                src={editingPreviewUrl || "/placeholder.svg"}
                                alt="New Preview"
                                className="w-full h-full object-cover"
                              />
                            </div>
                            <div className="text-sm text-gray-600">
                              <p className="font-medium">Will replace current image</p>
                              <p>‚Ä¢ Upload to Cloudinary on save</p>
                              <p>‚Ä¢ Automatically optimized</p>
                            </div>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Display Order</label>
                    <input
                      type="number"
                      value={editingMember.order || 0}
                      onChange={(e) =>
                        setEditingMember({ ...editingMember, order: Number.parseInt(e.target.value) || 0 })
                      }
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      min="0"
                      disabled={uploading}
                    />
                  </div>
                </div>
                <div className="flex justify-end gap-3 mt-8">
                  <button
                    onClick={clearEditingForm}
                    disabled={uploading}
                    className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors disabled:opacity-50"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={handleUpdateMember}
                    disabled={uploading}
                    className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                      uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"
                    }`}
                  >
                    {uploading ? (
                      <>
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                        Saving...
                      </>
                    ) : (
                      <>
                        <Save size={18} />
                        Save Changes
                      </>
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}